import React from 'react'
import { Component } from 'react'
import { Container, Form, FormText,Button,Card } from 'react-bootstrap'
const {Configuration, OpenAIApi} = require("openai");


class ProductDescription extends Component {
    constructor(){
        super()
        this.state = {
            heading: 'This Response from the AI will be shown here',
            response: '...await the response'
        }
    }

    onFormSubmit = e => {
        e.preventDefault()

        const formData = new FormData(e.target),
        formDataObject = Object.fromEntries(formData.entries())
        console.log(formDataObject.productName)

        const configuration = new Configuration({
            apiKey: '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',
        });
        const openai = new OpenAIApi(configuration);

        openai.createCompletion({
            model: "text-davinci-002",
            prompt: `Write a detailed, smart, informative and professional product description for ${formDataObject.productName}`,
            temperature: 0.8,
            max_tokens: 256,
            top_p: 1,
            frequency_penalty: 0,
            presence_penalty: 0,
        })
        .then((response)=> {
            this.setState({
                heading: `Product Description Suggestions for: ${formDataObject.productName}`,
                response: `${response.data.choices[0].text}`
            })
        });
    }
    render (){
        return(
            <div>
                <Container>
                    <br/>
                    <br/>
                    <h1>Generate Product Description</h1>
                    <br/>

                    <Form onSubmit = {this.onFormSubmit}>
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            
                            <Form.Control 
                                text="text"
                                name="productName"
                                placeholder="Enter Product Name" 
                            />
                            <FormText className="text-muted">
                                Enter Details...more the merrier
                            </FormText>
                        </Form.Group>

                        <Button variant = "primary" size = "lg" type = "submit">
                            Get AI suggestions
                        </Button>
                    </Form>
                    <br/>
                    <br/>
                    <Card>
                        <Card.Body>
                            <Card.Title><h1>{this.state.heading}</h1></Card.Title>
                            <hr/>
                            <br/>
                            <Card.Text>
                                <h4>
                                    {this.state.response}
                                </h4>
                            </Card.Text>
                        </Card.Body>
                    </Card>
                </Container>
                <br/>
                <br/>
                <br/>
                <br/>
            </div>
        );
    }
}

export default ProductDescription



